<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- КОНВЕРСИИ -->
    <v-tab
      key="tab-conversions"
      :value="0"
      @click="selectTab('conversions')"
    >
      {{ $t('tracker.tabs.conversions') }}
    </v-tab>

    <!-- POSTBACK URL -->
    <v-tab 
      key="tab-postback"
      :value="1"
      @click="selectTab('postback')"
    >
      {{ $t('tracker.tabs.postback') }}
    </v-tab>
  </v-tabs>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'TrackerTabs',

  data: () => ({
    tabs: ['conversions', 'postback'],
  }),

  computed: {
    ...mapGetters({
      activeTab: 'tracker/activeTab',
    }),

    tabNumber() {
      if (typeof this.$route.params.tab === 'undefined' || this.$route.params.tab === 'conversions') {
        return 0;
      } else if (this.$route.params.tab === 'postback') {
        return 1; 
      }
      return 0;
    }
  },

  methods: {
    changeTab(tab) {
      this.$store.dispatch('tracker/setActiveTab', {
        tab: tab,
        component: this.tabs[tab]
      });
    },

    selectTab(tab) {
      return this.$route.params.tab === tab ? true : this.$router.push({path: `/tracker/${tab}`, params: {tab: tab}});
    }
  }
};
</script>