<template>
  <div style="padding: 8px;">
    <dialog-import v-if="dialogs.dialogImport" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import DialogImport                      from '@/components/tracker/conversions/ConversionsDialogImport.vue';
import MainTable                         from '@/components/tracker/conversions/ConversionsMainTable.vue';
import Topbar                            from '@/components/tracker/conversions/ConversionsTopbar.vue';
import {mapGetters}                      from 'vuex';

export default {
  name: 'Conversions',

  components: {
    DialogImport,
    MainTable,
    Topbar
  },

  data() {
    return {
      updateInterval: null
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'conversions/dialogs',
    })
  },

  created() {
    this.$store.dispatch('conversions/loadConversions');
    this.updateInterval = setInterval(() => {
      this.$store.dispatch('conversions/loadConversions');
    }, 60000);
  },

  beforeDestroy() {
    clearInterval(this.updateInterval);
  },
};
</script>