<template>
  <v-col
    cols="12"
    md="12"
    xl="8"
    offset-xl="2"
  >
    <!-- POSTBACK URL -->
    <v-text-field
      v-model="postback.postbackURL"
      :label="$t('tracker.postback.postbackURL')"
      outlined
      dense
      hide-details
      @click="selectInput($event)"
    >
      <template #append>
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn
              icon
              x-small
              @click="copyToClipboard(postback.postbackURL)"
              v-on="on"
            >
              <v-icon>content_copy</v-icon>
            </v-btn>
          </template>
          <span>{{ $t('common.copy') }}</span>
        </v-tooltip>
      </template>
    </v-text-field>
  </v-col>
</template>

<script>
import clipboard       from '@/mixins/clipboard';

export default {
  name: 'PostbackCardCurrency',

  mixins: [clipboard],

  props: {
    postback: {
      type: Object,
      default: () => ({})
    }
  },

  watch: {
    postback: {
      deep: true,
      handler() {
        this.postback.postbackURL = 
          'http://' + location.host + '/postback' + 
          '?ad_id=' + this.postback.ad_id + 
          '&lead_status=' + this.postback.lead_status + 
          '&sale_status=' + this.postback.sale_status + 
          '&declined_status=' + this.postback.declined_status + 
          '&trash_status=' + this.postback.trash_status + 
          '&status=' + this.postback.status + 
          '&ext_id=' + this.postback.ext_id + 
          '&sum=' + this.postback.sum + 
          '&currency=' + this.postback.currency + 
          '&pp=' + this.postback.pp;
      }
    }
  },

  created() {
    this.postback.postbackURL = 
      'http://' + location.host + '/postback' + 
        '?ad_id=' + this.postback.ad_id + 
        '&lead_status=' + this.postback.lead_status + 
        '&sale_status=' + this.postback.sale_status + 
        '&declined_status=' + this.postback.declined_status + 
        '&trash_status=' + this.postback.trash_status + 
        '&status=' + this.postback.status + 
        '&ext_id=' + this.postback.ext_id + 
        '&sum=' + this.postback.sum + 
        '&currency=' + this.postback.currency + 
        '&pp=' + this.postback.pp;
  },

  methods: {
    selectInput(event) {
      event.path[0].select();
    }
  }
};
</script>