<template>
  <div>
    <tabs />
    <div>
      <component :is="componentName" />
    </div>
  </div>
</template>

<script>
import Tabs                     from '@/components/tracker/TrackerTabs.vue';
import Conversions              from '@/views/tracker/Conversions.vue';
import Postback                 from '@/views/tracker/Postback.vue';

export default {
  name: 'Tracker',

  components: {
    Tabs,
    Conversions,
    Postback
  },

  computed: {
    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'conversions';
    }
  }
};
</script>