<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          :value="conversions.selected"
          :headers="cols"
          :items="conversions.all"
          :loading-text="$t('common.loading')"
          fixed-header
          :items-per-page="100"
          :footer-props="{'items-per-page-options': [10, 30, 50, 100, -1]}"
          :height="innerHeight - 50 - 48 - 36"
          :sort-by="'id'"
          :sort-desc="true"
          show-select
          @input="selectItem"
        >
          <!-- EXT_ID -->
          <template #item.ext_id="{item}">
            <span class="pr-2">{{ item.ext_id }}</span>
          </template>

          <!-- СТАТУС -->
          <template #item.status="{item}">
            <main-table-status :item="item" />
          </template>

          <!-- СУММА -->
          <template #item.sum="{item}">
            {{ numeral(item.sum).format('0,0.[00]').split(',').join(' ') }}
          </template>

          <!-- СУММА -->
          <template #item.sum_usd="{item}">
            {{ '$' + numeral(item.sum_usd).format('0,0.[00]').split(',').join(' ') }}
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import numeral                                      from 'numeral';
import {mapGetters}                                 from 'vuex';

import MainTableStatus                              from './ConversionsMainTableStatus.vue';

export default {
  name: 'ConversionsMainTable',

  components: {
    MainTableStatus
  },

  data() {
    return {
      cols: [
        {
          text: 'ID',
          value: 'id',
          width: 50,
        },
        {
          // text: this.$t('tracker.conversions.extId'),
          text: 'EXT ID',
          value: 'ext_id',
          width: 100,
        },
        {
          text: this.$t('tracker.conversions.adId'),
          value: 'ad_id',
          width: 200,
        },
        {
          text: this.$t('tracker.conversions.time'),
          value: 'datetime',
          width: 200,
        },
        {
          text: this.$t('tracker.conversions.status'),
          value: 'status',
          width: 200,
        },
        {
          text: this.$t('tracker.conversions.sum'),
          value: 'sum',
          width: 200,
        },
        {
          text: this.$t('tracker.conversions.sumUsd'),
          value: 'sum_usd',
          width: 100,
        },
        {
          text: this.$t('tracker.conversions.extId'),
          value: 'pp',
          // width: 200,
        }
      ],
      numeral
    };
  },

  computed: {
    ...mapGetters({
      conversions: 'conversions/conversions',
      innerHeight: 'main/innerHeight',
    })
  },

  methods: {
    selectItem(item) {
      this.$store.dispatch('conversions/setSelected', item);
    }
  }

};
</script>