<template>
  <div>
    <!-- КНОПКА УДАЛИТЬ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="conversions.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.deleteConversion"
            @click="deleteConversions"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <!-- ИНДИКАТОР -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <div
        class="body-2 ml-3"
        style="padding-bottom: 3px;"
        :style="{color: conversions.selected.length > 0 ? '' : 'gray', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ conversions.selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'ConversionsTopbarActions',

  computed: {
    ...mapGetters({
      loading: 'conversions/loading',
      conversions: 'conversions/conversions',
    })
  },
  methods: {
    deleteConversions() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let data = {
        ids: this.conversions.selected.map(conversion => conversion.id)
      };
      this.$store.dispatch('conversions/deleteConversion', data);
    },
  }
};
</script>